@font-face {
  font-family: "Cascadia Mono 400";
  src: url("./fonts/CascadiaMono-Regular.ttf") format("truetype");
}

$nav-height: 70px;
$remaining-height: calc(100vh - $nav-height);

$overallTheme-1: #2c3e50;
$overallTheme-2: #8aa8b7;
$otherTheme-1: #ffffff;
$otherTheme-2: #3498db;
$otherTheme-3: #4f2b1e;
$otherTheme-4: #ffd5ab;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
li,
button {
  color: $otherTheme-1;
  font-family: "Microsoft JhengHei", sans-serif;
  font-weight: bold;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
}

li {
  list-style-type: none;
}

button {
  border: none;
  background: none;
}

body {
  background: $overallTheme-1;
}

@import "./nav";
@import "./main";
